import Article from './components/article'

export const articleList = [
  {
    id: '3-knigi',
    date: '2018-11-09',
    title: '3 knigi',
    fbLink: 'http://example.com'
  },
  {
    id: 'chem-ukraina',
    date: '2018-11-02',
    title: 'Chem Ukraina'
  },
]

export function renderArticle(id, content) {
  const meta = articleList.find(a => a.id === id)
  return <Article meta={meta} content={content} />
}
